.oval {
    width: 100px;
    height: 50px;
    background: #a84909;
    border-radius: 50%;
    text-align: center;
  }

.introgif{
  justify-content: center;
  align-items: center;
  flex: auto;
  width: 1100px;
  height: 650px;
}

 @media only screen and (max-width: 1366px) { /* tab */

  .item-card {
    width: 1rem;
    background-color: #fff;
    border: 1px solid#f1f2f6;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    padding: 0.1rem 2rem 0.1rem 0.4rem;
    border-radius: 0.3rem;
    margin: 0.1rem;
    position: relative;
    text-align: center;
  }


  .video {
    width: 100%;
    /* background-color: #a84909; */
    border: 1px solid#f1f2f6;
    display: ruby-base;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    /* padding: 0.1rem 2rem 0.1rem 0.4rem; */
    border-radius: 0.3rem;
    margin: 0.1rem;
    position: inherit;
    text-align: center;
  }


  .introgif{
    justify-content: center;
    align-items: center;
    flex: auto;
    width: 500px;
    height: 400px;
  }
 
}